<template>
  <div id="app">
    <h3>流调快捷拨号</h3>

    <el-alert center title="使用说明" type="success" effect="dark">
      <p>
        可以实现快速拨打手机号,需要上传 Excel 文件,文件格式有强制要求(<el-button
          size="small"
          type="text"
          @click="handleDownDemo"
        >
          下载模版 </el-button
        >). 只能
        <b style="color: red">手机</b>
        打开该网页才可以实现快速拨号
      </p>
      <p>
        项目是纯前端,不会上传后端信息,请放心使用
        <a href="./static/author.jpeg" target="_black"> 联系作者 </a>
      </p>
    </el-alert>

    <div class="btn-group">
      <el-upload
        action=""
        :multiple="false"
        :limit="1"
        :show-file-list="false"
        :before-upload="handleBeforeUpload"
        accept=".xls,.xlsx"
      >
        <el-button size="small" type="primary" class="upload-btn">
          点击上传
        </el-button>
      </el-upload>
      <el-button size="small" type="primary" @click="handleDownTable">
        下载文件
      </el-button>
    </div>
    <el-table border size="mini" :data="tableBody">
      <el-table-column label="姓名" prop="姓名" width="50" />
      <el-table-column label="电话" prop="电话" width="100">
        <template slot-scope="scope">
          <a :href="`tel:${scope.row['电话']}`"> {{ scope.row["电话"] }}</a>
        </template>
      </el-table-column>
      <el-table-column label="状态" prop="状态" width="60">
        <template slot-scope="scope">
          <span :class="`status-${scope.row['状态']}`">
            {{ valueShowLabel(scope.row["状态"], statusList) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-radio-group v-model="scope.row['状态']">
            <el-radio
              size="mini"
              v-for="(item, index) in statusList"
              :key="index"
              :label="item.value"
              >{{ item.label }}</el-radio
            >
          </el-radio-group>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { read, utils, writeFileXLSX } from "xlsx";
import { formatKey } from "./tools";
export default {
  name: "App",
  data() {
    return {
      tableBody: [],
      statusList: [
        {
          value: "success",
          label: "正常",
        },
        {
          value: "error",
          label: "未接通",
        },
        {
          value: "warning",
          label: "跟进",
        },
      ],
      demoBody: [
        {
          姓名: "",
          电话: "",
        },
      ],
    };
  },
  methods: {
    valueShowLabel: (value, arr) => {
      if (!value) return "";
      if (!Array.isArray(arr)) throw new Error("arr 不为数组");
      let active = arr.find((item) => `${item.value}` === `${value}`);
      if (!active) return `${value}` || "";

      return active["label"] || "";
    },
    async handleBeforeUpload(file) {
      try {
        var formData = await file.arrayBuffer();
        const wb = read(formData);
        const data = utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
        const newData = [];
        data.forEach(function (item) {
          console.log({ item });
          let newItem = {};
          Object.keys(item).forEach((key) => {
            let newKey = formatKey(key);
            newItem[newKey] = item[key];
          });

          this.push(newItem);
        }, newData);

        this.tableBody = newData;
        return Promise.reject();
      } catch (error) {
        console.log(error);
        let { message } = error;
        this.$message.error(message);
      }
    },
    handleDownDemo() {
      let { demoBody } = this;
      this.exportFile(demoBody);
    },
    // 下载结果
    handleDownTable() {
      let { tableBody } = this;
      const newData = [];
      let _this = this;

      tableBody.forEach(function (item) {
        let { 姓名, 电话 = "", 状态 } = item;
        状态 = _this.valueShowLabel(状态, _this.statusList) || "未标注";
        Object.assign(item, { 姓名, 电话, 状态 });
        this.push(item);
      }, newData);

      this.exportFile(newData);
    },
    exportFile(data) {
      console.log({ data });
      const ws = utils.json_to_sheet(data);
      const wb = utils.book_new();
      utils.book_append_sheet(wb, ws, "Data");
      writeFileXLSX(wb, `${new Date().getTime()}.xlsx`);
    },
  },
  components: {},
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 2vh;
}
.btn-group {
  margin: 5px 0;
  display: flex;
}
.btn-group div {
  margin: 0 5px 0 0;
}
.status-success {
  color: #67c23a;
}
.status-error {
  color: #909399;
}
.status-warning {
  color: #f56c6c;
}
</style>
